/* ==========================================================================
    Global
 ========================================================================== */
/* Global Colors*/
:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #873e95;
    --purple-dk: #6c2d79;
    --pink: #e83e8c;
    --orange: #f7941d;
    --orange-dk:#f26522;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --info2: #1e96a8;
    --warning: #f4bf00;
    --danger: #ed1c24;
    --light: #f8f9fa;
    --dark: #55595c;
    --green: #8fc54f;    
    --green-dk: #6d9142;    
    --yellow: #f4bf00;
    --red: #ed1c24;
  --border-radius: 0.3rem;
}

.page {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  position: fixed;
  width: 100vw;
}
 
html,
body {
    font-family: 'Montserrat', sans-serif;
    font-weight:normal;
    overflow-x:hidden;
    -webkit-font-smoothing: antialiased;
    color: #2d3038;
    font-weight: 500;
}
p {
    font-size: 1.15rem;
    line-height: 1.8rem;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: #2d3038;
    margin-top: 10px;
    margin-bottom: 10px;    
}

ul {
    margin:0;
    padding:0;
    list-style:none;
}

a{
    color: #2d3038;
}

a:hover {
    color: #00aeef;
}

a, a:hover, a:active,
a:focus, button:focus, .btn:focus,
.form-control:focus {
    outline: none !important;
    text-decoration: none !important;
    box-shadow: none !important;
}
.small, small {
    font-size: 85%;
    font-weight: 400;
    line-height: 1rem;
}

.small, small h3 {
    font-size: 65%;
    font-weight: 500;
    line-height: 1rem;
}
.section-padding {
    padding-top: 50px;
    padding-bottom: 60px;
}

.section-title {
    margin-bottom: 60px;
}

.section-title h1 {
    font-size: 56px;
}

.main-section .section-title h1 {
    font-size: 50px;
    margin-top: 0;
}

.bg-color {
    background-color: #f9f9f9;
    position: relative;
}

.bg-color:before {
    position: absolute;
    content: "";
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
    display: block;
}

.section-content {
    max-width: 750px;
}

.btn.btn-orange {
    background-color: #f26522;
    background-color: var(--orange-dk);
    border-color: #f7941d;
    border-color: var(--orange);
    font-weight: 600;
}
.btn.btn-orange:hover {
    background-color: #f7941d;
    background-color: var(--orange);
}

.btn.btn-purple {
    background-color: #873e95;
    background-color: var(--purple);
    border-color: #6c2d79;
    border-color: var(--purple-dk);
    font-weight: 500;
    color: white;
}
.btn.btn-purple:hover {
    background-color: #6c2d79;
    background-color: var(--purple-dk);
}
.btn.btn-primary {
    padding-left: 30px;
    padding-right: 30px;
    font-size: 1.3 rem;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #3c70b4;
    border: 1px solid #3c70b4;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.btn.btn-primary:before {
    position: absolute;
    content: "";
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: -1;
}

.btn.btn-primary:hover:before {
    left: 0;
}

.btn.btn-primary:hover {
    color: #3c70b4;
}
/* custom badge */
.badge {
    padding: 8px;
    margin: 3px 3px;
}
.badge-warning {
    color: #fff;
    background-color: #f4bf00;
    background-color: var(--yellow);
}
.badge-orange {
    color: #fff;
    background-color: #f7941d;
    background-color: var(--orange);
}
.badge-purple {
    color: #fff;
    background-color: #873e95;
    background-color: var(--purple);
}
/* ==========================================================================
    Home Banner
 ========================================================================== */

/* Navigation */

#navigation.menu-one .navbar {
    width: 30%;
    left: inherit;
    right: 0;
    padding: 0;
}

#navigation .navbar-nav>li {
    background-color: #2d3038;
}

#navigation.menu-one .navbar-nav>li {
    float: none;

    position: relative;
    margin-left: 10px;    
}

#navigation.menu-one .navbar-nav {
    float: none;
    flex: inherit;
}

#navigation.menu-one .navbar-nav> li {
    width: 100%;
}

#navigation.menu-one .navbar-nav> li:hover,
#navigation.menu-one .navbar-nav> li.current {
    margin-left: 0;
    padding-left: 10px;
}
 
#navigation.menu-one .navbar-nav> li:last-child:hover,
#navigation.menu-one .navbar-nav> li:last-child.current {
    margin-left: 10px;
    padding-left: 0;
}

#navigation .navbar-nav>li a {
    color: #fff;
    font-size: .9rem;
    font-weight: 600;
    text-transform: uppercase;
    display: block;
    height: 100%;
    width: 100%;
    padding: 0;
}

#navigation.menu-one .navbar-nav>li span {
    position: absolute;
    top: 50%;
    left: 90px;
    margin-top: -8px;
}

#navigation .navbar-nav>li i {
    position: absolute;
    left: -50px;
    font-size: 30px;
    margin-top: -6px;
}

.nav>li>a:focus, 
.nav>li>a:hover {
    background-color: transparent;
}

#navigation.menu-one .navbar:before {
    left: 15px;
    right: -100%;
} 

#navigation.menu-one .navbar:after {
   display: none;
}

#navigation.menu-one .navbar-header {
    position: absolute;
    top: 0;
}

.home-one .logo {
    height: 65px;
    float: none;
    margin-left: 10px;
    padding: 4px 30px;
}

.home-one .navbar-nav {
    margin-top: 65px;
}

#navigation .navbar-nav>li:nth-child(1) {
    background-color: #f4bf00;
}

#navigation .navbar-nav>li:nth-child(2) {
    background-color: #f7941d;
}

#navigation .navbar-nav>li:nth-child(3) {
    background-color: #ed1c24;
}

#navigation .navbar-nav>li:nth-child(4) {
    background-color: #873e95;
}

#navigation .navbar-nav>li:nth-child(5) {
    background-color: #87c05e;
}

#navigation .navbar-nav>li:nth-child(6) {
    background-color: #05ac80;
}

#navigation .navbar-nav>li:nth-child(7) {
    background-color: #873e95;
}

#navigation .navbar-nav>li:nth-child(8) {
    background-color: #3c70b4;
}

#navigation .navbar-nav>li:nth-child(9) {
    background-color: #05ac80;
}

#navigation .navbar-nav>li:last-child {
    background-color: #87c05e;
}

#navigation .navbar-nav>li .social {
    margin-left: 25px;
    overflow: hidden;
}

#navigation .navbar-nav>li .social li {
   float: left;
}

#navigation .navbar-nav>li .social li a {
    padding: 20px 15px;
}

#navigation .navbar-nav>li ul li {
    padding: 0;
}

#navigation .navbar-nav>li ul li i {
    font-size: 1.3 rem;
    margin-right: 0;
    position: inherit;
}

#navigation .navbar-nav>li ul li a {
    padding: 10px;
}

#navigation .navbar-nav>li ul li a:hover i {
    font-size: 1.1 rem;
    color: #000;
}

/* main-section */

#navigation .navbar {
    border: 0;
    padding: 0;
}

#navigation.menu-two .navbar-nav>li>a {
    padding: 28px 16px;
    display: block;
    position: inherit !important;
}

#navigation.menu-two .navbar-nav>li.current a,
#navigation.menu-two .navbar-nav>li a:hover {
    padding-bottom: 40px;
}

.current {
    padding-bottom: 40px !important;

}

#navigation.menu-two .navbar-nav>li:last-child {
    background-color: #3c70b4;
}

#navigation.menu-two .navbar {
    display: block;
}

#navigation.menu-two .navbar-header {
    float: left;
}

#navigation.menu-two .navbar-nav>li {
    float: left;
}

#navigation.menu-two .navbar-nav {
    display: block;
}


#navigation .navbar:before,
#navigation .navbar:after {
    position: absolute;
    content: "";
    top: 0;
    width: 100%;
    height: 76px;
    background-color: #2d3038;
    z-index: -1;
}

#navigation .navbar:before {
    right:0;
    background-color: #2d3038;
    left: -100%;
}

#navigation .navbar:after {
    left: 50%;
    background-color: #3c70b4;
    right: 0;
    z-index: -1;
}

.logo {
    background-color: #2d3038;
    height: 76px;
    padding: 9px 25px 10px 15px;
    margin-right: 0;
}

.menu-two .fixed-top {
    max-width: 1170px;
    margin: 0 auto;
}


/*Dropdown CSS*/

ul.sub-menu {
    position: absolute;
    top: 100%;
    left: 0;
    list-style: none;
    padding:7px 0;
    margin: 0;
    min-width: 160px; 
    display: none;
    z-index: 999;
    background-color: #f4bf00;
}
  
.nav.navbar-nav > li:hover > ul.sub-menu {
    display: block;
    -webkit-animation: fadeInUp 500ms;
    animation: fadeInUp 500ms;
}

#navigation.menu-two .navbar-nav .sub-menu li a {
    padding: 5px 18px;
    color: #fff;
    font-size: 1 rem;
}

@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

