
/* ==========================================================================
    Global
 ========================================================================== */
/* Global Colors*/
:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #873e95;
    --purple-dk: #6c2d79;
    --pink: #e83e8c;
    --orange: #f7941d;
    --orange-dk:#f26522;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --info2: #1e96a8;
    --warning: #f4bf00;
    --danger: #ed1c24;
    --light: #f8f9fa;
    --dark: #55595c;
    --green: #8fc54f;    
    --green-dk: #6d9142;    
    --yellow: #f4bf00;
    --red: #ed1c24;
  --border-radius: 0.3rem;
}

.page {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  position: fixed;
  width: 100vw;
}


@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700|Sacramento');
 
html,
body {
    font-family: 'Montserrat', sans-serif;
    font-weight:normal;
    overflow-x:hidden;
    -webkit-font-smoothing: antialiased;
    color: #2d3038;
    font-weight: 500;
}
p {
    font-size: 1.15rem;
    line-height: 1.8rem;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: #2d3038;
    margin-top: 10px;
    margin-bottom: 10px;    
}

ul {
    margin:0;
    padding:0;
    list-style:none;
}

a{
    color: #2d3038;
}

a:hover {
    color: #00aeef;
}

a, a:hover, a:active,
a:focus, button:focus, .btn:focus,
.form-control:focus {
    outline: none !important;
    text-decoration: none !important;
    box-shadow: none !important;
}
.small, small {
    font-size: 85%;
    font-weight: 400;
    line-height: 1rem;
}

.small, small h3 {
    font-size: 65%;
    font-weight: 500;
    line-height: 1rem;
}
.section-padding {
    padding-top: 50px;
    padding-bottom: 60px;
}

.section-title {
    margin-bottom: 60px;
}

.section-title h1 {
    font-size: 56px;
}

.main-section .section-title h1 {
    font-size: 50px;
    margin-top: 0;
}

.bg-color {
    background-color: #f9f9f9;
    position: relative;
}

.bg-color:before {
    position: absolute;
    content: "";
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
    display: block;
}

.section-content {
    max-width: 750px;
}

.btn.btn-orange {
    background-color: var(--orange-dk);
    border-color: var(--orange);
    font-weight: 600;
}
.btn.btn-orange:hover {
    background-color: var(--orange);
}

.btn.btn-purple {
    background-color: var(--purple);
    border-color: var(--purple-dk);
    font-weight: 500;
    color: white;
}
.btn.btn-purple:hover {
    background-color: var(--purple-dk);
}
.btn.btn-primary {
    padding-left: 30px;
    padding-right: 30px;
    font-size: 1.3 rem;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #3c70b4;
    border: 1px solid #3c70b4;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.btn.btn-primary:before {
    position: absolute;
    content: "";
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: -1;
}

.btn.btn-primary:hover:before {
    left: 0;
}

.btn.btn-primary:hover {
    color: #3c70b4;
}
/* custom badge */
.badge {
    padding: 8px;
    margin: 3px 3px;
}
.badge-warning {
    color: #fff;
    background-color: var(--yellow);
}
.badge-orange {
    color: #fff;
    background-color: var(--orange);
}
.badge-purple {
    color: #fff;
    background-color: var(--purple);
}
