.blog-section .entry-post {
    margin-bottom: 30px;
    overflow: hidden;
}

.entry-thumbnail {
    position: relative;
    z-index: -1;
    overflow: hidden;
}

.thumbnail-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(211, 211, 210, 0.9); 
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
}
.overlay-text {
    margin: 20px;
    text-align: center;
    line-height: 2rem;
}

.overlay-text p  {
    color: var(--gray-dark);
    font-weight: 500;
    font-size: 1.3rem;

}

.entry-post:hover .thumbnail-overlay {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
    border-radius: 0%;
}

.entry-post .time {
    margin-top: -45px;
    z-index: 1;
}

.entry-post .time a {
    width: 45px;
    height: 45px;
    color: #fff;
    font-size: 36px;    
    background-color: #f4bf00;
    line-height: 60px;
    text-align: center;
    border-radius: 50%;
    display: block;
    font-weight: 700;
    text-transform: uppercase;
    -webkit-transition: all 900ms;
    -moz-transition: all 900ms;
    -ms-transition: all 900ms;
    -o-transition: all 900ms;
    transition: all 900ms;
}

.blog-section .entry-post:hover .time a {
    border-radius: 0%;
}

.post-content .time.time1 a {
    background-color: #f26522;
}

.post-content .time.time2 a {
